import { Link } from 'react-router-dom';
import { CircleDefinition } from '../../../provider/connections/CirlceDataTypes';
import Circles from '../../Icons/Circles/Circles';

const CircleMembershipView = ({
  circleDef,
  permissionLevel,
}: {
  circleDef: CircleDefinition;
  permissionLevel?: string;
}) => {
  if (!circleDef) {
    return <></>;
  }

  return (
    <div className="mb-4">
      <Link to={`/owner/circles/${circleDef.id}`}>
        <h2 className="mb-2 flex flex-row text-xl">
          <Circles className="my-auto mr-2 h-4 w-4" />{' '}
          <span className="my-auto">{circleDef.name}</span>
          {permissionLevel && <span className="my-auto">: {permissionLevel}</span>}
        </h2>
      </Link>
    </div>
  );
};

export default CircleMembershipView;
