import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import AppManagementProvider from '../../provider/app/AppManagementProvider';
import useAuth from '../auth/useAuth';

const useApps = () => {
  const queryClient = useQueryClient();
  const { getSharedSecret } = useAuth();

  const appManagementProvider = AppManagementProvider.getInstance(getSharedSecret());

  const fetchRegistered = async () => {
    return await await appManagementProvider.GetAppRegistrations();
  };

  const revokeApp = async ({ appId }: { appId: string }) => {
    // return await circleNetworkRequestProvider.deleteSentRequest(targetDotYouId);
  };

  return {
    fetchRegistered: useQuery(['registeredApps'], () => fetchRegistered(), {
      refetchOnMount: false,
      refetchOnWindowFocus: false,
    }),
    revokeApp: useMutation(revokeApp, {
      onSuccess: () => {
        queryClient.invalidateQueries(['registeredApps']);
      },
      onError: (ex) => {
        console.error(ex);
      },
    }),
  };
};

export default useApps;
